@use "./colours.scss" as *;

@mixin flex {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-grow: 0;
    flex-shrink: 0;
}

@mixin hide {
    opacity: 0;
    animation: none;
}

.navMenuMargin {
    margin: 0;
    padding: 0;
    width: 100%;
    height: 50px;
}

.mainSection {
    width: 100%;
    max-width: 800px;
    height: auto;
    margin: 0 auto;
    @include flex;
    flex-direction: column;

    box-sizing: border-box;
}

.mainSection > h1 {
    font-size: 1.5rem;
    margin: 5px;
    color: $fonts_color;
    text-shadow: 0 0 1px rgb(117, 117, 117);
    text-align: center;
    position: relative;
}
.mainSection > h1::after {
    content: "";
    position: absolute;
    bottom: -2px;
    left: calc(50% - 200px);
    height: 0.2rem;
    background: $fonts_color;
    width: 400px;
}

.stopScrolling {
    overflow: hidden;
}

.fullScreenImg {
    display: none;
    position: fixed;
    z-index: 9999;
    width: 100dvw;
    height: 100dvh;
    background: rgba(0, 0, 0, 0.9);
    background-size: contain;
    background-repeat: no-repeat no-repeat;
    background-position: center center;
}

