@mixin flex {
    display: flex;
    justify-content: center;
    align-items: center;
}

@mixin hide {
    opacity: 0;
    animation: none;
}

.txtHighlight {
    color: $highlight_color;
    font-weight: 700;
    text-decoration: none;
}

.txtHighlightNoHover {
    font-weight: 700;
    text-decoration: none;
}

.txtHighlight:hover {
    color: $highlight_color_hover;
    font-weight: 700;
    text-decoration: underline;
    cursor: pointer;
}

.btn {
    margin: 1rem;
    padding: 1rem;
    color: $fonts_color;
    background: $bg_color_grey;
    // box-shadow: 0 0 5px 1px rgb(0, 0, 0);
    border-radius: 1rem;
    border-width: 3px;
    border-style: outset;
    text-transform: uppercase;
    text-decoration: none;
    font-size: 0.8rem;
    overflow: hidden;
    transition: all 0.2s;
    transition: transform 0.2s;
    font-weight: 700;
    text-align: center;
    cursor: pointer;
    position: relative;
    overflow: visible;
}

.btn > img {
    position: absolute;
    bottom: -25px;
    right: -25px;
    height: 50px;
    width: 50px;
}

.btn:hover {
    background: $highlight_color_hover;
    box-shadow: 0 0 5px 1px $highlight_color_hover;
}
